import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { NzMessageService, UploadFilter, UploadFile } from 'ng-zorro-antd';
import { Observable, Observer } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { environment } from 'src/environments/environment.prod';
import { EventoService } from 'src/app/services/evento.service';

@Component({
  selector: 'app-persona-carga',
  templateUrl: './persona-carga.component.html',
  styleUrls: ['./persona-carga.component.css']
})
export class PersonaCargaComponent implements OnInit {
  procesando:boolean = false;
  verListaPersonal: boolean = false;

  seleccionadoEvento:boolean = false;

  verModadlAsignarEvento:boolean = false;
  listaEventos:Array<object> = [];
  eventoSeleccionado:number;
  textoEventoSeleccionado:string;
  idProcesoSeleccionado:number;

  listaPersonal:Array<object> = [];

  fileList = [

  ];

  filters: UploadFilter[] = [
    {
      name: 'type',
      fn: (fileList: UploadFile[]) => {
        const filterFiles = fileList.filter(w => ~['application/vnd.openxmlformats-officedocument.spreadsheetml.sheet'].indexOf(w.type));
        console.log('filterFiles: ' + filterFiles);
        if (filterFiles.length !== fileList.length) {
          this.msg.error(`Solo se admiten archivos del tipo csv`);
          return filterFiles;
        }
        return fileList;
      }
    },
    {
      name: 'async',
      fn: (fileList: UploadFile[]) => {
        return new Observable((observer: Observer<UploadFile[]>) => {
          // doing
          observer.next(fileList);
          observer.complete();
        });
      }
    }
  ];
  constructor(private msg: NzMessageService, private http: HttpClient, private eventoService:EventoService) { }

  ngOnInit() {
    this._loadLists();
  }

  private _loadLists(){
    this.procesando = true;

    // Load Data:
    this.procesando = false;
  }

  uploadFileName = '';

  handleUpload = (item: any) => {
    const formData = new FormData();
    this.procesando = true;
    // formData.append(item.name, item.file as any, this.uploadFileName);
    formData.append('file', item.file as any, this.uploadFileName);
    // const url_app = 'http://104.41.14.101:8083/api/personal/upload/';
    const url_app = environment.host+'personal/upload/';
    formData.append('usuarioEjecucion', 'VPEREZ');
    // this.http.post('https://jsonplaceholder.typicode.com/posts/', formData).subscribe(
    this.http.post(url_app, formData).subscribe(
      res => {
        console.log("success", JSON.stringify(res));
        this.listaPersonal = res['data']  || [];
        this.idProcesoSeleccionado = Number(res['message']);
        item.onSuccess(item.file);
        this.procesando = false;
        this.verListaPersonal = true;
      },
      (err) => {
        item.onError(err, item.file);
        this.procesando = false;
      }
    );
  }

  beforeUpload = (file: UploadFile): boolean => {
    if(file.type=='application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' || file.type=='application/vnd.ms-excel'){
      this.uploadFileName = file.name;
      return true;  
    }else{
      this.msg.error('Solo se admiten archivos del tipo csv');
      return false;
    }
  }
  // tslint:disable-next-line:no-any
  handleChange(info: any): void {
    const fileList = info.fileList;
    // 2. read from response and show file link
    if (info.file.response) {
      info.file.url = info.file.response.url;
    }
    // 3. filter successfully uploaded files according to response from server
    // tslint:disable-next-line:no-any
    this.fileList = fileList.filter((item: any) => {
      if (item.response) {
        return item.response.status === 'success';
      }
      return true;
    });
  }

  irEditar(data:object){

  }

  asignarEvento(){
    this.seleccionadoEvento = true;
  }



  matricular(){
    this.procesando = true;
    this.eventoService.matricular(this.eventoSeleccionado, this.idProcesoSeleccionado).toPromise()
      .then(res => {
        if(res['codigo'] == 100){
          this.msg.success("La operación fue realizada con éxito");
        } else{
          this.msg.error('ocurrió un error al realizar esta operación');
        }
        this.procesando = false;
      }, err=>{
        this.procesando = false;
        this.msg.error('ocurrió un error al realizar esta operación');
      })
  }

  onListarEventos(event){
    this.listaEventos = event;
  }

  seleccionarEvento(idEvento:number){
    this.seleccionadoEvento = false;
    this.eventoSeleccionado = idEvento;
    this.listaEventos.forEach(e =>{
      if(e['idEvento'] == this.eventoSeleccionado){
        this.textoEventoSeleccionado = e['nombreEvento'];
      }
    })
  }
}
