import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { PersonasShellComponent } from './personas-shell/personas-shell.component';
import { PersonaListarComponent } from './persona-listar/persona-listar.component';
import { PersonaNuevoComponent } from './persona-nuevo/persona-nuevo.component';
import { PersonaEdicionComponent } from './persona-edicion/persona-edicion.component';
import { PersonaCargaComponent } from './persona-carga/persona-carga.component';
import { PersonaBajaComponent } from './persona-baja/persona-baja.component';


const routes: Routes = [{
  path: '', component: PersonasShellComponent, children: [
    { path: '', redirectTo: 'carga'},
    { path: 'lista', component: PersonaListarComponent},
    { path: 'nuevo', component: PersonaNuevoComponent },
    { path: 'modificar/:id', component: PersonaEdicionComponent},
    { path: 'carga', component: PersonaCargaComponent},
    { path: 'baja', component: PersonaBajaComponent},
  ]}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class PersonasRoutingModule { }
