import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-upload-personas',
  templateUrl: './upload-personas.component.html',
  styleUrls: ['./upload-personas.component.css']
})
export class UploadPersonasComponent implements OnInit {

  public isCollapsed: boolean = false;

  constructor() { }

  ngOnInit() {
  }

  options = [
    {
      icon: 'file-search',
      title: 'Lista Personal',
      url: 'lista'
    },
    {
      icon: 'file-add',
      title: 'Carga de Personal',
      url: 'nuevo'
    },
  ];

  public handleCollapse() {
    window.innerWidth < 768 ? this.isCollapsed = true : null;
  }

  public collapseChange() {
    window.innerWidth < 768 ? this.isCollapsed = false : null;
  }
}
