import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

@Component({
  selector: 'app-personas-shell',
  templateUrl: './personas-shell.component.html',
  styleUrls: ['./personas-shell.component.css']
})
export class PersonasShellComponent implements OnInit {

  public isCollapsed: boolean = false;

  constructor() { }

  ngOnInit() {
  }

  options = [
    {
      icon: 'file-add',
      title: 'Carga de Personal',
      url: 'carga'
    },
  ];

  public handleCollapse() {
    window.innerWidth < 768 ? this.isCollapsed = true : null;
  }

  public collapseChange() {
    window.innerWidth < 768 ? this.isCollapsed = false : null;
  }
}


